import React from "react";
import { useTranslation } from "react-i18next"; 
import { useTheme } from "../../context/ThemeContext";
import styles from "./MarketUpdateRow.module.scss"; 

const MarketUpdateHeader = () => {
    const { theme } = useTheme();
    const { t } = useTranslation();

    return (
        <div className={`${styles.row} ${styles.header} ${theme === 'dark' ? styles.dark : ''}`}>
            <div className={styles.rank}>#</div>
            <div className={styles.coin}>
                <div className={styles.name}>{t("marketUpdate.name")}</div>
            </div>
            <div className={styles.price}>{t("marketUpdate.lastPrice")}</div> 
            <div className={styles.change}>{t("marketUpdate.change24h")}</div> 
            <div className={styles.marketCap}>{t("marketUpdate.marketCap")}</div> 
            <div className={styles.sparkline}>{t("marketUpdate.last7Days")}</div> 
            <div className={styles.tradeButtonDiv}></div>
        </div>
    );
};

export default MarketUpdateHeader;
